import React from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import { Constants } from '../../constants/Common/Common';
import { AREA_TEXT } from '../../constants/Screens/Screens';
import PropTypes from 'prop-types';

const useStyle = makeStyles(() => {
  return createStyles({
    layoutFloorPlanImage: {
      height: '200px',
      marginBottom: '24px',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundSize: 'cover',
      display: 'flex',
      justifyContent: 'center',
    },
  });
});

const FloorPlanCards = ({ data, viewImage, available }) => {
  const classes = useStyle();
  const { bathrooms, bedrooms, name, livingArea, livingAreaMeasure, media } = data;
  const { downloadUrl } = media[0];
  return (
    <div className='col-lg-4 col-md-4 col-sm-12 col-xs-12 mb-3'>
      <div className='app-container content_center'>
        <div
          style={{
            cursor: 'pointer',
          }}
          className={classes.layoutFloorPlanImage}
          onClick={() =>
            viewImage(
              bathrooms,
              bedrooms,
              name,
              livingArea,
              livingAreaMeasure,
              downloadUrl
            )
          }
        >
          <img className='floorplan-img' alt={Constants.NO_IMAGE} src={downloadUrl} />
        </div>
        <h4>{name}</h4>
        <p>
          {bedrooms} {Constants.BEDROOM} | {bathrooms} {Constants.BATH} | {livingArea} {AREA_TEXT[livingAreaMeasure]}
        </p>
        <div className='layout_button_group_stacked'>
          <button
            className="target"
            type="button"
            button="primary"
            disabled={data.availableUnits === 0}
            onClick={() => available(data)}
          >
            {`(${data.availableUnits}) ${Constants.AVAILABLE}`}
          </button>
        </div>
      </div>
    </div>
  );
};

FloorPlanCards.propTypes = {
  data: PropTypes.object.isRequired,
  viewImage: PropTypes.func.isRequired,
  available: PropTypes.func.isRequired,
};

export default FloorPlanCards;
