import React from 'react';
import PropTypes from 'prop-types';

const CustomRangeFilter = ({ label, options, onChange, type, symbol }) => {
  return (
    <div className="form-group col-6 col-sm-6 col-md-3">
      <label>{label}</label>
      <select id={type}  onChange={(event) => onChange(event.target.value, type)} >
        <option value="0-0">All</option>
        {options &&
          options.map((item, i) => {
            const { minVal, maxVal } = item;
            return (
              <option key={i} value={`${minVal}-${maxVal}`}>
                { symbol === '$' ? `${symbol}${minVal}-${symbol}${maxVal}` : `${minVal}-${maxVal} ${symbol}`}
              </option>
            );
          })}
      </select>
    </div>
  );
};

CustomRangeFilter.propTypes = {
  label: PropTypes.string.isRequired,
  options: PropTypes.array.isRequired,
  onChange: PropTypes.func.isRequired,
  type: PropTypes.string.isRequired,
  symbol: PropTypes.string.isRequired,
};

export default CustomRangeFilter;
