
import React from 'react';
import { Screens } from '../../constants/Screens/Screens';
import { useDispatch } from 'react-redux';
import { setScreen } from '../../redux/slices/configurationSlice';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';

const InternalServerError = ({ error, resetErrorBoundary }) => {
  const dispatch = useDispatch();

  return (
    <div className={'internal_server_error'}>
      <div className={'align_center'}>
        <h1>{Constants.SOMETHING_WENT_WRONG}</h1>
        <h4>{Constants.INTERNAL_ERROR}</h4>
        <h3 >&nbsp;</h3>
        <pre>{error.message}</pre>
      </div>
      <div className={'align_center'}>
        <button
          className={'align_center target secondary '}
          onClick={() => {
            resetErrorBoundary();
            dispatch(setScreen(Screens.FLOORPLANS));
          }}
        >
          {Constants.GOTO_FLOORPLAN}
        </button>
      </div>
    </div>
  );
};

InternalServerError.propTypes = {
  error: PropTypes.object.isRequired,
  resetErrorBoundary: PropTypes.func.isRequired,
};

export default InternalServerError;
