import React from 'react';
import '../../styles/RentableItemCard.css';
import PropTypes from 'prop-types';
import { Constants } from '../../constants/Common/Common';

const RentableItemCard = ({ data, selectedRentableItems,  setSelectedRentableItems }) => {
  return (
    <div>
      <div className='app-container content_center'>
        <div className="item-name">{data.name}</div>
        <div className="item-checkbox">
          <input
            type="checkbox"
            onChange={(e) => {
              // add to list
              if (e.target.checked) {
                setSelectedRentableItems([
                  ...selectedRentableItems,
                  {
                    id: data.id,
                    name: data.name,
                    description: data.description,
                    price: data.price,
                  },
                ]);
              } else {
                // remove from list
                setSelectedRentableItems(
                  selectedRentableItems.filter((item) => item.id !== data.id),
                );
              }
            }}
          />

        </div>
        <div className="item-price">
          <div className="item-price-value">${data.price}</div>&nbsp; / {Constants.MONTH}
        </div>
        <div className="item-description">{data.description}</div>
      </div>
    </div>
  );
};

RentableItemCard.propTypes = {
  data: PropTypes.object.isRequired,
  selectedRentableItems: PropTypes.array.isRequired,
  setSelectedRentableItems: PropTypes.func.isRequired,
};

export default RentableItemCard;
