import { createSlice } from '@reduxjs/toolkit';
import { DEFAULT_LEASE_TERM_OPTIONS } from '../../constants/Filters/SelectOptionValue';

const initialSearchResultState = {
  floorPlans: {},
  apartments: {},
  leaseTerms: {},
  leaseTermOptions: DEFAULT_LEASE_TERM_OPTIONS,
};


const searchResultSlice = createSlice({
  name: 'searchResult',
  initialState: initialSearchResultState,
  reducers: {
    setFloorPlans(state, action) {
      state.floorPlans = action.payload;
    },
    setApartments(state, action) {
      state.apartments = action.payload;
    },
    setLeaseTerms(state, action) {
      state.leaseTerms = action.payload;
    },
    setLeaseTermOptions(state, action) {
      state.leaseTermOptions = action.payload;
    },
  },
});

export const { setFloorPlans, setApartments, setLeaseTerms, setLeaseTermOptions } = searchResultSlice.actions;

export default searchResultSlice.reducer;
