import { format } from 'date-fns';
import React from 'react';
import { useSelector } from 'react-redux';
import { AREA_TEXT, SELECTION_HEADER} from '../../constants/Screens/Screens';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';

const SelectionHeader = ({ title }) => {
  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const floorplan = useSelector((state) => { return state.userSelection.floorPlan; });
  const leaseterm = useSelector((state) => { return state.userSelection.leaseTerm; });
  const filters = useSelector((state) => { return state.searchFilter; });

  const buildHeaderText = () => {
    let text = '';
    if (Object.keys(floorplan).length !== 0) {
      text += `${floorplan.name} | ${floorplan.bedrooms} ${Constants.BEDROOM} | ${floorplan.bathrooms} ${Constants.BATH} | ${floorplan.livingArea} ${AREA_TEXT[floorplan.livingAreaMeasure]}`;

      if (Object.keys(apartment).length !== 0) {
        text += ` | ${apartment.unitNumber.includes('#') ? apartment.unitNumber : `#${apartment.unitNumber}`}`;

        if (Object.keys(leaseterm).length !== 0) {
          text += ` | ${Constants.MOVE_IN} ${format(new Date(filters.moveInDate), 'MM/dd/yyyy')} | ${filters.term} ${Constants.MONTH_LEASE}`;
        }
      }
    }
    return text;
  };

  return (
    <div id="PV_PageTitle">
      <section>
        <h2>{title}</h2>
        <p>
          {title === SELECTION_HEADER.FLOORPLANS ? '' :  buildHeaderText()}
        </p>
      </section>
    </div>
  );
};

SelectionHeader.propTypes = {
  title: PropTypes.string.isRequired,
};

export default SelectionHeader;
