import { createSlice } from '@reduxjs/toolkit';

const initialConfigurationState = {
  buildingId: '',
  apiUrl: '',
  pmc: '',
  page: 1,
  recordsPerPage: 10000,
  screen: 'floorplans',
  phoneNumber: '',
};

export const configurationSlice = createSlice({
  name: 'configuration',
  initialState: initialConfigurationState,
  reducers: {
    setConfiguration(state, action) {
      state.buildingId = action.payload.buildingId;
      state.apiUrl = action.payload.apiUrl;
      state.pmc = action.payload.pmc;
      state.phoneNumber = action.payload.phoneNumber;
    },
    setBuildingId(state, action) {
      state.buildingId = action.payload;
    },
    setPmc(state, action) {
      state.pmc = action.payload;
    },
    setApi(state, action) {
      state.apiUrl = action.payload;
    },
    setScreen(state, action) {
      state.screen = action.payload;
    },
    setPhoneNumber(state, action) {
      state.phoneNumber = action.payload;
    },
  },
});

export const { setConfiguration,
  setBuildingId,
  setPmc,
  setApi,
  setScreen,
  setPhoneNumber,

} = configurationSlice.actions;

export default configurationSlice.reducer;
