import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setScreen } from '../../redux/slices/configurationSlice';
import { setApartmentNumber, setLeaseTermMinDate, setMoveInDate } from '../../redux/slices/searchFilterSlice';
import { setApartment } from '../../redux/slices/userSelectionSlice';
import { AREA_TEXT, Screens } from '../../constants/Screens/Screens';
import { Constants } from '../../constants/Common/Common';
import NoResultsFound from '../../components/Common/NoResultsFound';
import PropTypes from 'prop-types';

const ApartmentResults = ({ apartments, filterProps }) => {
  const dispatch = useDispatch();
  const { phoneNumber } = useSelector((state) => { return state.configuration; });
  return (
    apartments.length > 0
      ?      <div id="PV_PageContent_Main">
        {
          apartments.map((apartment) =>
            <div key={apartment.id} className="layout_select_list_grouping app-container mb-4">
              <div className="content">
                <div className="content_row">
                  <div className="col-lg-2 col-md-2 col-sm-2 col-xs-12 py-0">
                    <div className="col-xs-6 col-sm-12 px-0 caption">{Constants.APARTMENT}</div>
                    <div className="col-sm-12 col-xs-6 detail">{apartment.unitNumber}</div>
                  </div>
                  <div className="col-lg-2 col-md-2 col-sm-3 col-xs-12 py-0">
                    <div className="col-xs-6 col-sm-12 px-0 caption">{Constants.AREA}</div>
                    <div className="col-sm-12 col-xs-6 detail">{apartment.floorPlan.livingArea} {AREA_TEXT[apartment.floorPlan.livingAreaMeasure]}</div>
                  </div>
                  <div className="col-lg-3 col-md-3 col-sm-3 col-xs-12 py-0">
                    <div className="col-xs-6 col-sm-12 px-0 caption">{Constants.PRICE_PER_MONTH}</div>
                    <div className="col-sm-12 col-xs-6 detail">
                      { apartment.minPrice ? `$${apartment.minPrice}` : '' } - { apartment.maxPrice ? `$${apartment.maxPrice}*` : '' }
                    </div>
                  </div>
                  <div className="col-lg-3 col-md-2 col-sm-1 col-xs-12 py-0"></div>
                  <div className="col-lg-2 col-md-3 col-sm-4 col-xs-12 button layout_button_group_stacked mt-0">
                    { apartment.minPrice && apartment.maxPrice
                      ? <button className="gold target"
                        button="primary"
                        onClick={async() => {
                        //await dispatch(setApartments(apartments));
                          await dispatch(setMoveInDate(filterProps.moveInDate));
                          await dispatch(setLeaseTermMinDate(filterProps.moveInDate));
                          await dispatch(setApartmentNumber(filterProps.apartmentNumber));
                          await dispatch(setApartment(apartment));
                          await dispatch(setScreen(Screens.LEASETERMS));
                        }}>
                        {Constants.SELECT}
                      </button>
                      : <a href={`tel:${phoneNumber}`}>
                        <button className="contact target" button="primary" href={`tel:${phoneNumber}`}>
                          {Constants.CONTACT_US}
                        </button>
                      </a>
                    }
                  </div>
                </div>
              </div>
            </div>
          )
        }
        <p className="pricing_note caption">{Constants.SUBJECT_TO_CHANGE}</p>
      </div>
      : <NoResultsFound label={Screens.APARTMENTS}/>
  );
};

ApartmentResults.propTypes = {
  apartments: PropTypes.array.isRequired,
  filterProps: PropTypes.object.isRequired,
};

export default ApartmentResults;
