import React from 'react';
import { Screens } from '../../constants/Screens/Screens';
import NoResultsFound from '../Common/NoResultsFound';
import RentableItemCard from './RentableItemCard';
import PropTypes from 'prop-types';

const RentableItemResults = ({ rentableItemResults, selectedRentableItems, setSelectedRentableItems }) => {
  return (
    rentableItemResults.length > 0
      ? rentableItemResults.map((data, index) => {
        return <div key={index}>
          <RentableItemCard
            data={data}
            selectedRentableItems={selectedRentableItems}
            setSelectedRentableItems={setSelectedRentableItems} />
        </div>;
      })
      : <NoResultsFound label={Screens.ITEMS} />
  );
};

RentableItemResults.propTypes = {
  rentableItemResults: PropTypes.array.isRequired,
  selectedRentableItems: PropTypes.array.isRequired,
  setSelectedRentableItems: PropTypes.func.isRequired,
};

export default RentableItemResults;
