import React from 'react';
import { TYPES } from '../../constants/PricingMatrix/SelectionType';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';

const PricingDetailsCard = ({ data, moveInDate, select, cardLabel}) => {
  return (
    <div className='col-lg-4 col-md-4 col-sm-12 col-xs-12 '
      style={{
        margin: cardLabel === TYPES.CURRENT_BEST ? 'auto' : '',
      }}
    >
      <div className={
        `app-container content_center ${
          cardLabel.includes(TYPES.BEST_VALUE) ? 'best-term' : ''}`
      }>
        <div className="term-title">{cardLabel}</div>
        <div className='details'>
          <div>
            <span
              style={{
                fontSize: '43px',
              }}>${data.price}</span>
            <span
              style={{
                fontSize: '20px',
              }}> {Constants.MONTHLY}</span>
          </div>
          <div className="text-sm muted mt-4" style={{
            fontSize: '16px',
          }}
          ><b>{data.term} {Constants.MONTH_LEASE}</b></div>

          <div className="mt-5">{Constants.MOVE_IN} {moveInDate}</div>
        </div>
        <div className='layout_button_group_stacked'>
          <button
            type="button"
            button="primary"
            onClick={() => select(data, moveInDate)}>
            {Constants.SELECT}
          </button>
        </div>
      </div>
    </div>
  );
};

PricingDetailsCard.propTypes = {
  data: PropTypes.object.isRequired,
  moveInDate: PropTypes.string.isRequired,
  select: PropTypes.func.isRequired,
  cardLabel: PropTypes.string.isRequired,
};

export default PricingDetailsCard;
