import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AREA_TEXT, Screens } from '../../constants/Screens/Screens';
import { Constants } from '../../constants/Common/Common';
import { setLeaseTermMinDate, setMoveInDate } from '../../redux/slices/searchFilterSlice';
import NoResultsFound from '../Common/NoResultsFound';
import PropTypes from 'prop-types';

function ApartmentCards({ data, selectApartment, currentMoveInDate }) {
  const dispatch = useDispatch();
  const { phoneNumber } = useSelector((state) => { return state.configuration; });
  return (
    <>
      {data.length > 0 ? (<div>
      </div>) : <div><NoResultsFound label={Screens.APARTMENTS} /></div>}
      {data.map((item) => {
        return (
          <div key={item.id}>
            <p>
              {item.floorPlan.name} | {item.floorPlan.bedrooms} {Constants.BEDROOM} | {item.floorPlan.bathrooms} {Constants.BATH} | {item.floorPlan.livingArea} {AREA_TEXT[item.floorPlan.livingAreaMeasure]}
            </p>
            <div>
              <div className="layout_select_list_grouping app-container mb-4">
                <div className="content">
                  <div className="content_row">

                    <div className="col-lg-2 col-md-2 col-sm-2 col-xs-12 py-0">
                      <div className="col-sm-12 col-xs-6 px-0 caption">{Constants.APARTMENT}</div>
                      <div className="col-sm-12 col-xs-6 detail">{item.unitNumber}</div>
                    </div>
                    <div className="col-lg-2 col-md-2 col-sm-3 col-xs-12 py-0">
                      <div className="col-sm-12 col-xs-6 px-0 caption">{Constants.AREA}</div>
                      <div className="col-sm-12 col-xs-6 detail"> {item.floorPlan.livingArea} {AREA_TEXT[item.floorPlan.livingAreaMeasure]} </div>
                    </div>
                    <div className="col-lg-3 col-md-3 col-sm-3 col-xs-12 py-0">
                      <div className="col-sm-12 col-xs-6 px-0 caption">{Constants.PRICE_PER_MONTH}</div>
                      <div className="col-sm-12 col-xs-6 detail">{item.minPrice ? `$${item.minPrice}` : ''} - {item.maxPrice ? `$${item.maxPrice}*` : ''}</div>
                    </div>
                    <div className="col-lg-3 col-md-2 col-sm-1 col-xs-12 py-0"></div>
                    <div className="col-lg-2 col-md-3 col-sm-4 col-xs-12 button layout_button_group_stacked mt-0">
                      {(() => {
                        if (item.minPrice && item.maxPrice) {
                          if (item.showFutureDate) {
                            return <div>
                              <div>
                                <button
                                  className="gold"
                                  onClick={() => {
                                    selectApartment(item);
                                    dispatch(setMoveInDate(item.formattedAvailableDate));
                                    dispatch(setLeaseTermMinDate(item.formattedAvailableDate));
                                  }}>
                                  Select
                                </button>
                              </div>
                              <div className="pt-3"
                                style={{
                                  fontSize: '12px',
                                }}>
                                {Constants.AVAILABLE_FROM.replace('#AVAILABLE_DATE', item.availableDate)}
                              </div>
                            </div>;
                          } else {
                            return <button
                              className="gold"
                              onClick={() => {
                                selectApartment(item);
                                dispatch(setMoveInDate(currentMoveInDate));
                                dispatch(setLeaseTermMinDate(currentMoveInDate));
                              }}>
                              {Constants.SELECT}
                            </button>;
                          }
                        } else {
                          return <div className="button">
                            <a href={`tel:${phoneNumber}`}>
                              <button className="gold" button="primary">
                                {Constants.CONTACT_US}
                              </button>
                            </a>
                          </div>;
                        }
                      })()}
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </div>
        );
      })}
    </>
  );
}

ApartmentCards.propTypes = {
  data: PropTypes.array.isRequired,
  selectApartment: PropTypes.func.isRequired,
  currentMoveInDate: PropTypes.string.isRequired,
};

export default ApartmentCards;
