import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useForm } from 'react-hook-form';
import { AsYouType, isValidNumber } from 'libphonenumber-js';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import PasswordStrengthBar from 'react-password-strength-bar';
import { getPmcInformation, submitRegistration } from '../../services/UserRegistrationQuery/userRegistrationQueries';
import Spinner from '../Common/Spinner';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';
import { useErrorHandler } from 'react-error-boundary';

const formatPhoneNumber = (value) => {
  if (!value) { return ''; }
  value = value.toString();
  if (value.includes('(') && !value.includes(')')) {
    return value.replace('(', '');
  }
  return new AsYouType('CA').input(value);
};

const UserRegistrationModal = ({ openModal, selectedRentableItems }) => {
  const handleError = useErrorHandler();
  const moveInDate = useSelector((state) => { return state.searchFilter.moveInDate; });
  const { pmc } = useSelector((state) => { return state.configuration; });
  const pricingId = useSelector((state) => { return state.userSelection.leaseTerm.id; });
  const [password, setPassword] = useState('');
  const [confirm, setConfirm] = useState('');
  const [headerError, setHeaderError] = useState('');
  const [passwordShown, setPasswordShown] = useState(false);
  const [confirmShown, setConfirmShown] = useState(false);
  const [loading, setLoading] = useState(false);
  const [termsOfUse, setTermsOfUse] = useState('');
  const [pmcName, setPmcName] = useState('');

  const formSchema = Yup.object().shape({
    firstName: Yup.string()
      .required(Constants.FIRST_NAME_REQUIRED),
    lastName: Yup.string()
      .required(Constants.LAST_NAME_REQUIRED),
    email: Yup.string()
      .required(Constants.EMAIL_REQUIRED)
      .matches(/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i, Constants.EMAIL_INVALID),
    phone: Yup.string()
      .required(Constants.PHONE_NUMBER_REQUIRED)
      .test(
        'validatePhone',
        Constants.PHONE_NUMBER_INVALID,
        (value) => isValidNumber(value, 'CA') || isValidNumber(value, 'US')
      ),
    password: Yup.string()
      .required(Constants.PASSWORD_REQUIRED)
      .min(8, Constants.PASSWORD_MINIMUM)
      .matches(
        /^.*(?=.{8,})((?=.*[!@#$%^&*()\-_=+{};:,<.>]){1})(?=.*\d)((?=.*[a-z]){1})((?=.*[A-Z]){1}).*$/,
        Constants.PASSWORD_VALIDATION
      ),
    confirm: Yup.string()
      .required(Constants.CONFIRM_PASSWORD_REQUIRED)
      .oneOf([Yup.ref('password')], Constants.CONFIRM_PASSWORD_MISMATCH),
    agree: Yup.boolean()
      .oneOf([true], Constants.TERMS_OF_USE_REQUIRED),
  });
  const formOptions = { resolver: yupResolver(formSchema) };
  const { register, handleSubmit, formState: { errors } } = useForm(formOptions);


  const getPmcInfo = async () => {
    try {
      const pmcInformation = await getPmcInformation(pmc);
      setTermsOfUse(pmcInformation.termsOfUse);
      setPmcName(pmcInformation.pmcName || pmc);

    } catch (error) {
      handleError(error);
    } finally {
      setLoading(false);
    }
  };

  // init onload
  useEffect(() => {
    setLoading(true);
    const delayDebounceFn = setTimeout(() => {
      getPmcInfo();
    }, 500);

    return () => clearTimeout(delayDebounceFn);
  }, []);


  const onSubmit = (data) => {
    setLoading(true);
    setHeaderError('');
    const featureIds = (selectedRentableItems !== undefined) ? [...selectedRentableItems.map((item) => item.id)] : [];
    const body = { ...data, moveInDate, pricingId, featureIds };
    if (body.agree) { delete body.agree; }
    if (body.confirm) { delete body.confirm; }
    let url = '';
    const delayDebounceFn = setTimeout(async () => {
      try {
        url = await submitRegistration(body);
        window.open(url, '_self');
      } catch (error) {
        setHeaderError(Constants.ERROR_ENCOUNTERED);
        handleError(error);
      } finally {
        setLoading(false);
      }
    }, 2000);
    return () => clearTimeout(delayDebounceFn);
  };

  return (
    <div className='modal display-block calendar' id='staticBackdrop'>
      <div className='modal-dialog modal-lg modal-dialog-centered' style={{ maxWidth: '600px' }}>
        <div className='modal-content'>
          <div className='modal-header modal-header-with-bg '>
            <p className='mx-3' style={{
              color: 'white',
              fontSize: 19,
              fontWeight: 'bold',
              marginTop: 20,
            }}>{Constants.REGISTER_NOW}</p>
          </div>
          <div className='modal-body' style={{ margin: '10px' }}>
            {loading ? <Spinner /> : ''}
            <form onSubmit={handleSubmit(onSubmit)} style={loading ? { opacity: '50%', zIndex: '0' } : { opacity: '100%' }}>
              {headerError && <p style={{ color: 'red', fontSize: '12px' }}>{headerError}</p>}
              <div className="row">
                <p className="pb-3 fs-5">{Constants.REGISTER_YOUR_ACCOUNT}</p>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.FIRST_NAME}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register('firstName')}
                  />
                  {errors.firstName && <p style={{ color: 'red', fontSize: '12px' }}>{errors.firstName.message}</p>}
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.LAST_NAME}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register('lastName')}
                  />
                  {errors.lastName && <p style={{ color: 'red', fontSize: '12px' }}>{errors.lastName.message}</p>}
                </div>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.EMAIL}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register('email')}
                  />
                  {errors.email && <p style={{ color: 'red', fontSize: '12px' }}>{errors.email.message}</p>}
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.PHONE_NUMBER}</label>
                  <input
                    type="text"
                    className='form-control'
                    maxLength={16}
                    {...register('phone', {
                      onChange: (e) => e.target.value = formatPhoneNumber(e.target.value),
                    }
                    )}
                  />
                  {errors.phone && <p style={{ color: 'red', fontSize: '12px' }}>{errors.phone.message}</p>}
                </div>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.PASSWORD}</label>
                  <input
                    type="password"
                    className='form-control icon_showpassword'
                    onMouseDown={() => setPasswordShown(true)}
                    onMouseUp={() => setPasswordShown(false)}
                    onBlur={() => setPasswordShown(false)}
                    onSelect={() => setPasswordShown(false)}
                    {...register('password', {
                      onChange: (e) => setPassword(e.target.value),
                    })}
                  />
                  {passwordShown &&
                    <span className='showpassword'
                      onClick={() => setPasswordShown(false)}
                      onSelect={() => setPasswordShown(false)}
                    >
                      {password}
                    </span>
                  }
                  {errors.password && <p style={{ color: 'red', fontSize: '12px' }}>{errors.password.message}</p>}
                  <PasswordStrengthBar password={password} scoreWordStyle={{ fontSize: '10px' }} />
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{Constants.CONFIRM_PASSWORD}</label>
                  <input
                    type="password"
                    className='form-control icon_showpassword'
                    onMouseDown={() => setConfirmShown(true)}
                    onMouseUp={() => setConfirmShown(false)}
                    onBlur={() => setConfirmShown(false)}
                    onSelect={() => setConfirmShown(false)}
                    {...register('confirm', {
                      onChange: (e) => setConfirm(e.target.value),
                    })}
                  />
                  {confirmShown &&
                    <span className='showpassword'
                      onClick={() => setConfirmShown(false)}
                      onSelect={() => setConfirmShown(false)}
                    >
                      {confirm}
                    </span>
                  }
                  {errors.confirm && <p style={{ color: 'red', fontSize: '12px' }}>{errors.confirm.message}</p>}
                </div>
              </div>
              <div className="mt-2 mb-5" style={{ wordWrap: 'unset' }}>
                <input type="checkbox" className='form-control' style={{ position: 'fixed' }} {...register('agree')} />
                <span style={{ marginLeft: '30px' }}>{Constants.AGREE_WITH} {pmcName} <a target="_blank" href={`${termsOfUse}`} rel="noopener noreferrer">{Constants.TERMS_OF_USE}</a></span>
                {errors.agree && <p style={{ color: 'red', fontSize: '12px' }}>{errors.agree.message}</p>}
              </div>
              <div className="row">
                <div className='col-xs-push-12' style={{ textAlign: 'right' }}>
                  <button type='reset' className="target secondary"
                    style={{ marginRight: '5px' }}
                    button="secondary"
                    onClick={openModal}
                  >
                    {Constants.CANCEL}
                  </button>
                  <button className="target primary"
                    button="primary"
                    type="submit"
                    disabled={loading}
                  >
                    {Constants.REGISTER}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

UserRegistrationModal.propTypes = {
  openModal: PropTypes.func.isRequired,
  selectedRentableItems: PropTypes.array.isRequired,
};

export default UserRegistrationModal;
