import React from 'react';
import { useSelector } from 'react-redux';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';

const ConfirmSelectionModal = ({ selectedRentableItems, closeModal, openRegistrationModal }) => {

  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const floorplan = useSelector((state) => { return state.userSelection.floorPlan; });
  const leaseterm = useSelector((state) => { return state.userSelection.leaseTerm; });

  const { name, bedrooms, bathrooms, livingArea, livingAreaMeasure } = floorplan;
  const { term, price } = leaseterm;
  const { unitNumber } = apartment;
  return (
    <div className='modal display-block' id='staticBackdrop'>
      <div className='modal-dialog modal-md modal-dialog-centered'>
        <div className='modal-content'>
          <div className='modal-header modal-header-with-bg '>
            <p className='mx-3' style={{
              color: 'white',
              fontSize: 19,
              fontWeight: 'bold',
              marginTop: 20,
            }}>{Constants.CONFIRM_SELECTION}</p>
          </div>

          <div className="modal-body">
            <p className="pb-3 fs-5">
              {Constants.FOLLOWING_SELECTIONS}
            </p>

            <div className="m-auto p-2 mt-4" style={{
              width: '340px',
            }}>

              <h4>{Constants.FLOORPLAN} - <span>{name}</span></h4>
              <table className="summary selectionsTable mt-0">
                <tbody>
                  <tr>
                    <td>{Constants.APARTMENT}</td>
                    <td><h4>{unitNumber}</h4></td>
                  </tr>
                  <tr>
                    <td>{Constants.BEDROOMS}</td>
                    <td><h4>{bedrooms}</h4></td>
                  </tr>
                  <tr>
                    <td>{Constants.BATHROOMS}</td>
                    <td><h4>{bathrooms}</h4></td>
                  </tr>
                  <tr>
                    <td>{livingAreaMeasure === 'sqMeters' ? Constants.SQUARE_METERS : Constants.SQUARE_FEET}</td>
                    <td><h4>{livingArea}</h4></td>
                  </tr>
                </tbody>
              </table>
              <hr></hr>
              <h4 className="mt-4">{Constants.LEASE_TERMS}</h4>
              <table className="summary selectionsTable">
                <tbody><tr>
                  <td>{Constants.TERM}</td>
                  <td><h4>{term} {Constants.MONTHS}</h4></td>
                </tr>
                <tr>
                  <td>{Constants.RENT}</td>
                  <td><h4>${price.toFixed(2)}</h4></td>
                </tr>
                </tbody>
              </table>
              <hr></hr>
              <h4 className="mt-4">{(selectedRentableItems === undefined) || (selectedRentableItems.length === 0) ? '' : Constants.RENTABLE_ITEMS} </h4>
              <table className="summary selectionsTable">
                <tbody>
                  { (selectedRentableItems !== undefined) ? selectedRentableItems.map((data) => {
                    return [
                      <tr key={data.id}>
                        <td>{data.name}</td>
                        <td><h4>{`$${data.price.toFixed(2)} / ${Constants.MONTH}`}</h4></td>
                      </tr>,
                    ];
                  }): ''}
                </tbody>
              </table>
            </div>
            <div className="row">
              <div className="">
                <div className="row">
                  <div className="col-md-6">
                    <button
                      type="button"
                      button="selections"
                      className="secondary target"
                      onClick={closeModal}
                    >{Constants.CANCEL}</button>
                  </div>
                  <div className="col-md-6">
                    <button
                      type="button"
                      button="selections"
                      className="continue"
                      onClick={openRegistrationModal}
                    >{Constants.CONTINUE}</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

ConfirmSelectionModal.propTypes = {
  selectedRentableItems: PropTypes.array.isRequired,
  closeModal: PropTypes.func.isRequired,
  openRegistrationModal: PropTypes.func.isRequired,
};

export default ConfirmSelectionModal;
