import React from 'react';
import { parseISO, isEqual } from 'date-fns';
import Day from './Day';
import PropTypes from 'prop-types';
import { Constants } from '../../../constants/Common/Common';

const CalendarGrid = ({ calendarDays, closeModal, saveModal, selectCalendarDate, selectedDate, selectedTerm }) => {
  return (
    <div>
      <div className="calendar-grid">
        {
          calendarDays.map((calendarDay, index) =>

            <Day key={index}
              data={calendarDay}
              first={index === 0}
              selectDate={selectCalendarDate}
              isSelectedDay={isEqual(parseISO(selectedDate), parseISO(calendarDay.date))}
            />
          )
        }
      </div>
      <div className="row">
        <div className="row">
          <p className="best-value">* Best Value</p>
        </div>
        <div className='col-xs-push-12' style={{ textAlign: 'right' }}>
          <button className="target secondary"
            style={{ marginRight: '5px' }}
            button="secondary"
            onClick={closeModal}
          >
            Cancel
          </button>
          <button className="target primary"
            button="primary"
            onClick={async () => {
              saveModal(selectedTerm, selectedDate);
            }}
          >
            {Constants.SAVE}
          </button>
        </div>
      </div>
    </div>
  );
};

CalendarGrid.propTypes = {
  calendarDays: PropTypes.array.isRequired,
  closeModal: PropTypes.func.isRequired,
  saveModal: PropTypes.func.isRequired,
  selectCalendarDate: PropTypes.func.isRequired,
  selectedDate: PropTypes.string.isRequired,
  selectedTerm: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
};

export default CalendarGrid;
