import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import FloorPlan from '../screens/FloorPlan';
import Apartment from '../screens/Apartment';
import Pricing from '../screens/Pricing';
import RentableItems from '../screens/RentableItems';
import { setConfiguration } from '../redux/slices/configurationSlice';
import { Screens } from '../constants/Screens/Screens';

function AppWrapper({ config }) {
  const dispatch = useDispatch();

  const validateConfig = () => {
    const requiredFields = ['apiUrl', 'buildingId', 'pmc'];
    if(!_.isEmpty(config)) {
      const errors = [];

      requiredFields.forEach( (key) => {
        if (!(key in config) || !config[key]) {
          errors.push(key);
        }
      });

      if (!_.isEmpty(errors)) {
        throw new Error(`There is an error with the following fields: ${errors.join(', ')}`);
      }

      return config;
    }

    throw new Error(`There is an error with the following fields: ${requiredFields.join(', ')}`);
  };

  dispatch(setConfiguration(validateConfig()));
  const configuration = useSelector((state) => { return state.configuration; });
  console.log(configuration);

  const renderScreen = () => {
    switch (configuration.screen) {
      case Screens.FLOORPLANS:
        return <FloorPlan />;
      case Screens.APARTMENTS:
        return <Apartment />;
      case Screens.LEASETERMS:
        return <Pricing />;
      case Screens.ITEMS:
        return <RentableItems />;
      default:
        return <FloorPlan />;
    }
  };

  useEffect(() => {
    renderScreen();
  }, [configuration.screen]);

  return (
    renderScreen()
  );
}

export default AppWrapper;
