import React from 'react';
import { useDispatch } from 'react-redux';
import { setScreen } from '../../redux/slices/configurationSlice';
import { setApartment, setLeaseTerm } from '../../redux/slices/userSelectionSlice';
import PropTypes from 'prop-types';
import { Screens } from '../../constants/Screens/Screens';

const BackButton = ({ screen }) => {
  const dispatch = useDispatch();
  return <div className='col-xs-push-12 container px-5'>
    <button className="target secondary"
      button="secondary"
      onClick={async () => {
        if (screen === Screens.APARTMENTS) {
          await dispatch(setApartment({}));
        }
        await dispatch(setLeaseTerm({}));
        await dispatch(setScreen(screen));
      }}
    >
      Back
    </button>
  </div>;
};

BackButton.propTypes = {
  screen: PropTypes.string.isRequired,
};

export default BackButton;
