import { format, parseISO } from 'date-fns';
import React from 'react';
import { TYPES } from '../../constants/PricingMatrix/SelectionType';
import { Screens } from '../../constants/Screens/Screens';
import { selectedMatchesTerm } from '../../services/utils/pricingHelper';
import NoResultsFound from '../Common/NoResultsFound';
import PricingDetailsCard from './PricingDetailsCard';
import PropTypes from 'prop-types';

const PricingResults = ({ pricingResults, pricingMoveInDate, bestValueTerm, greatValueTerm, pricingTerm, handleSelectLeaseTerm }) => {
  return (
    pricingResults.length > 0
      ? [
        pricingResults.map((leaseterm) => {
          return <PricingDetailsCard
            key={leaseterm.id}
            data={leaseterm}
            moveInDate={format(parseISO(pricingMoveInDate), 'MM/dd/yyyy')}
            select={handleSelectLeaseTerm}
            cardLabel={
              selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate)
                ? TYPES.CURRENT_BEST : TYPES.CURRENT_SELECTION
            }
          >
          </PricingDetailsCard>;
        }),
        !selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate) && Object.keys(bestValueTerm).length !== 0
          ? <PricingDetailsCard
            key={bestValueTerm.id}
            data={bestValueTerm}
            moveInDate={`${format(parseISO(bestValueTerm.displayDate), 'MM/dd/yyyy')}`}
            select={handleSelectLeaseTerm}
            cardLabel={TYPES.BEST_VALUE}
          />
          : '',
        !selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate) && !selectedMatchesTerm(greatValueTerm, pricingTerm, pricingMoveInDate) && Object.keys(greatValueTerm).length !== 0
          ? <PricingDetailsCard
            key={greatValueTerm.id}
            data={greatValueTerm}
            moveInDate={`${format(parseISO(greatValueTerm.displayDate), 'MM/dd/yyyy')}`}
            select={handleSelectLeaseTerm}
            cardLabel={TYPES.GREAT_VALUE}
          />
          : '',
      ]
      : <NoResultsFound label={Screens.LEASETERMS} />
  );
};

PricingResults.propTypes = {
  pricingResults: PropTypes.array.isRequired,
  pricingMoveInDate: PropTypes.string.isRequired,
  bestValueTerm: PropTypes.object.isRequired,
  greatValueTerm: PropTypes.object.isRequired,
  pricingTerm: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  handleSelectLeaseTerm: PropTypes.func.isRequired,
};

export default PricingResults;
