import React from 'react';
import PropTypes from 'prop-types';
import { Constants } from '../../constants/Common/Common';

const CustomDatePicker = ({ label, onBlur, value, type  }) => {
  return (
    <div className='form-group col-lg-6 col-6 col-md-6 col-sm-12 col-xs-12'>
      <label>{label}</label>
      <input
        type={type ? type : 'text'}
        value={value}
        className='form-control'
        placeholder={Constants.SEARCH_APARTMENT}
        onChange={(event) => onBlur(event.target.value, type)}
      />
    </div>
  );
};

CustomDatePicker.propTypes = {
  label: PropTypes.string.isRequired,
  onBlur: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
};

export default CustomDatePicker;
