import { store } from '../../redux/store';
import coreApi from '../../config/api';
import { apiData } from '../../constants/Api/path';

const getConfiguration = () => {
  return store.getState().configuration;
};

export const loadFloorPlan = async (body) => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    const { data } = await coreApi.post(`${apiData.urlFloorPlan}/${configuration.buildingId}?withMedia=true&page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`, body);
    const { records } = data;
    return { records };
  } catch (error) {
    throw error;
  }
};
