import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { format } from 'date-fns';
import '../styles/App.css';
import '../styles/Apartment.css';
import ApartmentFilter from '../components/Apartment/ApartmentFilter';
import ApartmentResults from '../components/Apartment/ApartmentResults';
import Spinner from '../components/Common/Spinner';
import BackButton from '../components/Common/BackButton';
import { Screens, SELECTION_HEADER } from '../constants/Screens/Screens';
import { loadUnits } from '../services/UnitQuery/unitQueries';
import { setScreen } from '../redux/slices/configurationSlice';
import SelectionHeader from '../components/Common/SelectionHeader';
import { useErrorHandler } from 'react-error-boundary';

function Apartment() {
  const dispatch = useDispatch();
  const handleError = useErrorHandler();

  const selection = useSelector((state) => { return state.userSelection; });
  const filters = useSelector((state) => { return state.searchFilter; });

  // initial value
  const floorPlanId = selection.floorPlan.id ? selection.floorPlan.id : '1';

  const [apartmentNumber, setApartmentNumber] = useState(filters.apartmentNumber || '');
  const [moveInDate, setMoveInDate] = useState(filters.moveInDate || format(new Date(), 'yyyy-MM-dd'));
  const [apartmentResults, setApartmentResults] = useState([]);
  const [loading, setLoading] = useState(true);

  // Simplify passing of state variables to Component props
  const filterProps = { apartmentNumber, setApartmentNumber, moveInDate, setMoveInDate };

  const getApartments = async () => {

    try {
      setLoading(true);

      const body = {
        floorPlanId,
        'unitNumber': apartmentNumber,
        moveInDate,
      };

      const { records } = await loadUnits(body);
      setApartmentResults(records);

    } catch (error) {
      handleError(error);
    } finally {
      setLoading(false);
    }
  };

  // init onload
  useEffect(() => {

    // refresh browser handling
    if (Object.keys(selection.floorPlan).length === 0) {
      dispatch(setScreen(Screens.FLOORPLANS));
    }

    const delayDebounceFn = setTimeout(() => {
      getApartments();
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }, [moveInDate, apartmentNumber]);

  return (
    <div>
      <div className="Apartment container px-5">
        <SelectionHeader title={SELECTION_HEADER.APARTMENTS} />
        <ApartmentFilter filterProps={filterProps} />
        { !loading
          ? <ApartmentResults apartments={apartmentResults} filterProps={filterProps} />
          : <Spinner /> }
      </div>
      { !loading
        ? <BackButton screen={Screens.FLOORPLANS} /> : ''
      }
    </div >
  );
}

export default Apartment;
