import React from 'react';
import { AREA_RANGE_VAL, BATHROOMS_OPTIONS, BEDROOMS_OPTIONS, DENS_OPTIONS, RENT_RANGE, SELECT_TYPE_LABEL } from '../../constants/Filters/SelectOptionValue';
import { CustomDatePicker, CustomRangeFilter, CustomSelect } from '../Filters';
import PropTypes from 'prop-types';
import { Constants } from '../../constants/Common/Common';

const FloorPlanFilter = ({ handleOnchange, selectedDate, handleSearch }) => {
  return <div>
    <div className='row'>
      <CustomDatePicker type="moveInDate" onChange={handleOnchange} value={selectedDate} />
      <CustomSelect
        label={SELECT_TYPE_LABEL.BEDROOMS}
        onChange={handleOnchange}
        options={BEDROOMS_OPTIONS}
        type={'bedrooms'}
      />
      <CustomSelect
        label={SELECT_TYPE_LABEL.BATHROOMS}
        onChange={handleOnchange}
        options={BATHROOMS_OPTIONS}
        type={'bathrooms'}
      />
    </div>
    <div className="row">
      <CustomSelect
        label={SELECT_TYPE_LABEL.DENS}
        onChange={handleOnchange}
        options={DENS_OPTIONS}
        type={'dens'}
      />

      <CustomRangeFilter
        label={SELECT_TYPE_LABEL.RENT_RANGE}
        options={RENT_RANGE}
        type="rent_range"
        onChange={handleOnchange}
        symbol="$"
      />

      <CustomRangeFilter
        label={SELECT_TYPE_LABEL.AREA_RANGE}
        options={AREA_RANGE_VAL}
        type="area_range"
        onChange={handleOnchange}
        symbol=""
      />
      <div className='form-group col-6 col-sm-6 col-md-3'>
        <label>{Constants.UNIT_NUMBER}</label>
        <input type='text' className='form-control' placeholder={Constants.SEARCH_APARTMENT} onChange={(e) => { e.persist(); handleSearch(e); }} />
      </div>
    </div>
  </div>;

};

FloorPlanFilter.propTypes = {
  handleOnchange: PropTypes.func.isRequired,
  selectedDate: PropTypes.string.isRequired,
  handleSearch: PropTypes.func.isRequired,
};

export default FloorPlanFilter;
