import React from 'react';
import PropTypes from 'prop-types';

const Day = ({ isSelectedDay, data, first, selectDate}) => {
  return (
    <div
      className={`calendar-day ${
        (data.isDisabled && 'disabled-day ') ||
        (isSelectedDay && 'selected-day')}`
      }
      style={{
        gridColumnStart: first ? 1 : 'auto',
      }}
      data-date={data.date}
      onClick={!data.isDisabled ? selectDate : null}
    >
      <p>{data.day}</p>
      <p className={data.isBestValue ? 'best-value' : ''}>{ data.price || '' }</p>
    </div>
  );
};

Day.propTypes = {
  isSelectedDay: PropTypes.bool.isRequired,
  data: PropTypes.object.isRequired,
  first: PropTypes.bool.isRequired,
  selectDate: PropTypes.func.isRequired,
};

export default Day;
