import React from 'react';
import { Screens } from '../../constants/Screens/Screens';
import FloorPlanCards from './FloorPlanCards';
import NoResultsFound from '../../components/Common/NoResultsFound';
import PropTypes from 'prop-types';

const FloorPlanResults = ({ floorPlanList, viewImage, handleSelectFloorPlan }) => {
  return (
        <>
          {floorPlanList.length > 0
            ? (
              floorPlanList.map((item, i) => {
                if (item.availableUnits !== 0) {
                  return <FloorPlanCards data={item} viewImage={viewImage} available={handleSelectFloorPlan} key={i} />;
                }
                return null;
              })
            ) : <div><NoResultsFound label={Screens.FLOORPLANS} /></div>}

        </>
  );
};

FloorPlanResults.propTypes = {
  floorPlanList: PropTypes.array.isRequired,
  viewImage: PropTypes.func.isRequired,
  handleSelectFloorPlan: PropTypes.func.isRequired,
};

export default FloorPlanResults;
