import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../styles/App.css';
import '../styles/Apartment.css';
import { loadFloorPlan } from '../services/FloorplanQuery/floorPlanQueries';
import { loadUnits } from '../services/UnitQuery/unitQueries';
import { setScreen } from '../redux/slices/configurationSlice';
import { setFloorPlan } from '../redux/slices/userSelectionSlice';
import { setApartmentNumber, setBathrooms, setBedrooms, setDen, setMoveInDate, setRentRange, setAreaRange } from '../redux/slices/searchFilterSlice';
import { setApartment } from '../redux/slices/userSelectionSlice';
import FloorPlanImageModal from '../components/Modals/FloorPlanImageModal';
import Spinner from '../components/Common/Spinner';
import { Screens, SELECTION_HEADER } from '../constants/Screens/Screens';
import { addMonths, endOfDay, endOfMonth, format, isAfter, isBefore, parseISO, startOfDay } from 'date-fns';
import { Constants } from '../constants/Common/Common';
import SelectionHeader from '../components/Common/SelectionHeader';
import { debounce } from 'lodash';
import ApartmentCards from '../components/FloorPlan/ApartmentCards';
import FloorPlanResults from '../components/FloorPlan/FloorPlanResults';
import FloorPlanFilter from '../components/FloorPlan/FloorPlanFilter';
import { useErrorHandler } from 'react-error-boundary';

function FloorPlan() {
  const dispatch = useDispatch();
  const handleError = useErrorHandler();
  const filters = useSelector((state) => { return state.searchFilter; });
  const getMoveInDate = () => {
    return filters.moveInDate || format(new Date(), 'yyyy-MM-dd');
  };
  const [selectedDate, setSelectedDate] = useState(getMoveInDate);
  const [selectedApartment, setSelectApartment] = useState('');
  const [showImageModal, setShowImageModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState({});
  const [loading, setLoading] = useState(false);

  const initialFieldValues = {
    moveInDate: selectedDate,
  };

  const [values, setValues] = useState(initialFieldValues);

  const handleOnchange = async (val, field) => {
    // OnChange Bathroom and Bedroom Filter
    if ((field === 'bedrooms' || field === 'bathrooms')) {
      let condition = 'eq';
      if (val === '4+' || val === '3+') {
        condition = 'greater_than_eq';
        val = val.replace('+', '');
      }
      if (val === '') {
        if (field === 'bedrooms') {
          delete values.bedrooms;
        } else {
          delete values.bathrooms;
        }
        setValues({
          ...values,
        });
      } else {
        setValues({
          ...values,
          [field]: {
            value: Number(val),
            condition,
          },
        });
      }
      dispatch(field === 'bedrooms' ? setBedrooms(val) : dispatch(setBathrooms(val)));
    }
    // OnChange Den Filter
    if (field === 'dens') {
      if (val !== '0' && val !== '1') {
        delete values.dens;
        setValues({
          ...values,
        });
      } else {
        setValues({
          ...values,
          [field]: val === 1,
        });
      }
      dispatch(setDen(val));
    }
    // OnChange RentRange Filter
    if (field === 'rent_range' && val) {
      const myArr = val.split('-');
      const min = myArr[0];
      const max = myArr[1];

      if (min === '0' && max === '0') {
        delete values.minPrice;
        delete values.maxPrice;
        setValues({
          ...values,
        });
      } else {
        setValues({
          ...values,
          minPrice: Number(min),
          maxPrice: Number(max),
        });
      }
      dispatch(setRentRange({ min, max }));
    }

    // OnChange Area Range
    if (field === 'area_range' && val) {
      const myArr = val.split('-');
      const min = myArr[0];
      const max = myArr[1];

      if (min === '0' && max === '0') {
        delete values.minLivingArea;
        delete values.maxLivingArea;
        setValues({
          ...values,
        });
      } else {
        setValues({
          ...values,
          minLivingArea: parseFloat(min),
          maxLivingArea: parseFloat(max),
        });
      }
      dispatch(setAreaRange({ min, max }));
    }

    // OnChange MoveInDate
    if (field === 'moveInDate' && val) {
      const moveDate = format(parseISO(val), 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000');
      setValues({
        ...values,
        [field]: moveDate,
      });
      setSelectedDate(val);
      dispatch(setMoveInDate(val));
    }
  };

  const handleSearch = debounce(async (e) => {

    setSelectApartment(e.target.value);
    const val = e.target.value;
    const v = document.querySelectorAll('input[type=date], select');
    if (val.length > 0) {
      v.forEach((opt) => {
        opt.disabled = true;
      });
    } else {
      v.forEach((opt) => {
        opt.disabled = false;
      });
    }

    dispatch(setApartmentNumber(val));
    try {
      const endOfNextMonth = endOfDay(endOfMonth(addMonths(parseISO(getMoveInDate()), 1)));
      const { records } = await loadUnits({ unitNumber: e.target.value, moveInDate: format(endOfNextMonth, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000') });
      const filtered = [];

      for (const apt of records) {
        const parsed = parseISO(apt.availableFrom);
        apt.availableDate = format(parsed, 'MM/dd');
        apt.showFutureDate = false;
        apt.formattedAvailableDate = format(parsed, 'yyyy-MM-dd');

        if (apt.status === Constants.AVAILABLE.toLowerCase()) {
          filtered.push(apt);
        } else if (isBefore(parsed, endOfNextMonth)) {
          if (isAfter(parsed, startOfDay(parseISO(selectedDate)))) {
            apt.showFutureDate = true;
          }
          filtered.push(apt);
        }
      }
      setFloorPlanApartmentList(filtered);
    } catch (error) {
      handleError(error);
    }
    finally {
      setLoading(false);
    }
  }, 1000);

  const [floorPlanList, setFloorPlanList] = useState([]);
  const [floorPlanListApartment, setFloorPlanApartmentList] = useState([]);

  const getFloorplan = debounce(async() => {
    try {
      const { records } = await loadFloorPlan(values);
      setFloorPlanList(records);
    } catch (error) {
      handleError(error);
    } finally {
      setLoading(false);
    }
  }, 1000);

  useEffect(() => {
    setLoading(true);
    getFloorplan();
  }, [values, filters]);

  const viewImage = (
    bathrooms,
    bedrooms,
    name,
    livingArea,
    livingAreaMeasure,
    downloadUrl
  ) => {
    setSelectedImage({
      bathrooms,
      bedrooms,
      name,
      livingArea,
      livingAreaMeasure,
      downloadUrl,
    });
    setShowImageModal(!showImageModal);
  };

  const closeViewImage = () => {
    setShowImageModal(!showImageModal);
  };

  const handleSelectApartment = async (apartment) => {
    await dispatch(setApartment(apartment.unitNumber));
    await dispatch(setApartment(apartment));
    await dispatch(setFloorPlan(apartment.floorPlan));
    await dispatch(setScreen(Screens.LEASETERMS));
  };

  const handleSelectFloorPlan = async (data) => {
    await dispatch(setFloorPlan(data));
    await dispatch(setScreen(Screens.APARTMENTS));
  };

  return (
    <div className='container px-5'>
      <SelectionHeader title={SELECTION_HEADER.FLOORPLANS} />
      <FloorPlanFilter handleOnchange={handleOnchange} selectedDate={selectedDate} handleSearch={handleSearch} />
      <div className='row'>
        {
          !loading
            ? (selectedApartment.length > 0 && <ApartmentCards data={floorPlanListApartment} selectApartment={handleSelectApartment} selectedDate={selectedDate} currentMoveInDate={getMoveInDate()} />) ||
              <FloorPlanResults floorPlanList={floorPlanList} viewImage={viewImage} handleSelectFloorPlan={handleSelectFloorPlan}/>
            : <Spinner />
        }
      </div>
      {showImageModal && (<FloorPlanImageModal closeModal={closeViewImage} data={selectedImage} />)}
    </div >
  );
}
export default FloorPlan;
