import React from 'react';
import { minDate } from '../../services/utils/minDate';
import PropTypes from 'prop-types';

const CustomDatePicker = ({ onChange, value, type, label, isBirthdate, mininmumDate = minDate()}) => {
  return (
    <div className="form-group col-12 col-sm-12 col-md-6">
      <label>{label}</label>
      { isBirthdate
        ? <input
          max={mininmumDate}
          id="birthDate"
          name="birthDate"
          type="date"
          className="icon_datepicker"
          value={value}
          onChange={(event) => onChange(event.target.value)}
        />
        :      <input
          min={mininmumDate}
          type="date"
          id="date"
          className="icon_datepicker"
          value={value}
          onChange={(event) => onChange(event.target.value, type)}
        />
      }
    </div>
  );
};

CustomDatePicker.propTypes = {
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  type: PropTypes.string,
  label: PropTypes.string,
  isBirthdate: PropTypes.bool,
  mininmumDate: PropTypes.string,
};


export default CustomDatePicker;
