import React, { useEffect, useState } from 'react';
import { SELECT_TYPE_LABEL } from '../../constants/Filters/SelectOptionValue';
import { CustomSelect } from '../Filters';
import { useSelector } from 'react-redux';
import {
  format,
  parseISO,
  setDate,
  addDays,
  subDays,
  addMonths,
  subMonths,
  getDate,
  getDaysInMonth,
  startOfMonth,
  endOfMonth,
  endOfDay,
  getISODay,
  isBefore,
} from 'date-fns';
import CalendarGrid from '../LeaseTerm/Calendar/CalendarGrid';
import Spinner from '../Common/Spinner';
import { loadUnitPricing } from '../../services/UnitQuery/unitQueries';
import { isBetweenPricingDates, selectedMatchesTerm } from '../../services/utils/pricingHelper';
import { SELECTION_HEADER } from '../../constants/Screens/Screens';
import SelectionHeader from '../Common/SelectionHeader';
import { Constants } from '../../constants/Common/Common';
import PropTypes from 'prop-types';

const PricingCalendarModal = ({ options, selectedMoveInDate, selectedTerm, closeModal, saveModal, bestValueTerm, minDate }) => {
  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const [pricingMoveInDate, setPricingMoveInDate] = useState(selectedMoveInDate);
  const [pricingTerm, setPricingTerm] = useState(selectedTerm);
  const [calendarDays, setCalendarDays] = useState([]);
  const [calendarSettings] = useState({
    date: setDate(parseISO(pricingMoveInDate), 1),
    today: new Date(),
  });
  const [currentMonthYear, setCurrentMonthYear] = useState(format(calendarSettings.date, 'MMMM yyyy'));
  const [isSelectedDate, setIsSelectedDate] = useState(false);
  const [loading, setLoading] = useState(true);
  const [pricingResults, setPricingResults] = useState({});

  const incrementMonth = () => {
    calendarSettings.date = addMonths(calendarSettings.date, 1);
    getCalendarDays(calendarSettings, pricingResults);
  };

  const decrementMonth = () => {
    calendarSettings.date = subMonths(calendarSettings.date, 1);
    getCalendarDays(calendarSettings, pricingResults);
  };

  const goToday = () => {
    calendarSettings.date = calendarSettings.today;
    getCalendarDays(calendarSettings, pricingResults);
    setPricingMoveInDate(format(calendarSettings.date, 'yyyy-MM-dd'));
  };

  const getUnitPricing = async() => {
    setLoading(true);
    const body = {};
    if (pricingTerm !== '') {body.term = pricingTerm;}
    try {
      const { records } = await loadUnitPricing(apartment.id, body);
      setPricingResults(records);
      getCalendarDays(calendarSettings, records);
    } catch (error) {
      throw error;
    }
  };

  const getCalendarDays = (calendarSettings, pricingResults) => {
    setCurrentMonthYear(format(calendarSettings.date, 'MMMM yyyy'));
    const daysInMonth = getDaysInMonth(calendarSettings.date);
    const dayOfWeek = getISODay(startOfMonth(calendarSettings.date));
    const min = parseISO(minDate);
    const dateFormat = 'yyyy-MM-dd';
    const days = [];

    for (let day = 1; day <= daysInMonth; day++) {
      // get last days of previous month to fill grid
      if (day === 1) {
        if (dayOfWeek !== 1) {
          const previousMonth = endOfMonth(subMonths(new Date(calendarSettings.date), 1));
          const previousMonthStartGridDate = subDays(previousMonth, dayOfWeek - 2);
          const previousMonthDays = getDaysInMonth(endOfMonth(new Date(previousMonth)));

          for (let i = getDate(previousMonthStartGridDate); i <= previousMonthDays; i++) {
            days.push({
              'day': i,
              'isDisabled': true,
              'date': format(addDays(new Date(previousMonthStartGridDate), 1), dateFormat),
              'price': null,
            });
          }
        }
      }

      const dateToAdd = endOfDay(setDate(new Date(calendarSettings.date), day));
      const price = pricingResults.length > 0 && !isBefore(dateToAdd, new Date()) ? getPriceFromResults(dateToAdd, pricingResults) : null;
      days.push({
        day,
        'isDisabled': isBefore(dateToAdd, new Date(min)) || price === null,
        'date': format(dateToAdd, dateFormat),
        'price': price !== null ? `$${  price}` : null,
        'isBestValue': selectedMatchesTerm(bestValueTerm, pricingTerm, format(dateToAdd, dateFormat)),
      });

      // get first days of next month to fill grid
      if (day === daysInMonth) {
        const lastDateOfWeek = setDate(new Date(calendarSettings.date), day);
        const lastDayOfWeek = getISODay(lastDateOfWeek);

        if (lastDayOfWeek !== 7) {
          let nextDay = new Date(lastDateOfWeek);
          for (let i = lastDayOfWeek; i < 7; i++) {
            nextDay = addDays(nextDay, 1);
            const futurePrice = pricingResults.length > 0 ? getPriceFromResults(nextDay, pricingResults) : null;
            days.push({
              'day': getDate(nextDay),
              'isDisabled': isBefore(nextDay, new Date(min)) || futurePrice === null,
              'date': format(nextDay, dateFormat),
              'price': futurePrice !== null ? `$${  futurePrice}` : null,
              'isBestValue': selectedMatchesTerm(bestValueTerm, pricingTerm, format(nextDay, dateFormat)),
            });
          }
        }
      }
    }
    setCalendarDays(days);
    setLoading(false);
  };

  const getPriceFromResults = (date, records) => {
    let price = null;

    for (const priceRecord of records) {
      if (isBetweenPricingDates(date, priceRecord)) {
        price = priceRecord.price;
        break;
      }
    }

    return price;
  };

  const selectCalendarDate = (event) => {
    setIsSelectedDate(!isSelectedDate);
    setPricingMoveInDate(event.currentTarget.dataset.date);
  };

  // init onload
  useEffect(() => {
    setLoading(true);
    const delayDebounceFn = setTimeout(() => {
      getUnitPricing();
    }, 500);

    return () => clearTimeout(delayDebounceFn);
  }, [pricingTerm]);

  return (
    <div className='modal display-block calendar' id='staticBackdrop'>
      <div className='modal-dialog modal-xl modal-dialog-centered'>
        <div className='modal-content'>
          <div className='modal-body' style={{margin:'10px'}}>
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                flexDirection: 'row',
                float: 'right',
              }}
            >
              <span
                className='modal_close_button'
                onClick={closeModal}
              >
              </span>
            </div>
            <div className="row mt-2">
              <div className="form-group col-6 col-sm-6 col-md-9">
                <SelectionHeader title={SELECTION_HEADER.CALENDAR} />
              </div>
              <CustomSelect label={SELECT_TYPE_LABEL.LEASETERM} options={options} onChange={setPricingTerm} value={pricingTerm} includeAll={false} />
            </div>
            <div className="row">
              <div className="nav">
                <div className="calendar-nav"><span onClick={decrementMonth}>&lsaquo;</span></div>
                <div className="calendar-title">{currentMonthYear}</div>
                <div className="calendar-nav"><span onClick={incrementMonth}> &rsaquo;</span></div>
              </div>
            </div>
            <div id="selected-date">
              <p style={{textAlign: 'right', cursor: 'pointer'}}><span onClick={goToday}>{Constants.TODAY}</span></p>
              <p style={{fontWeight: 'bold'}}>
                {Constants.SELECTED_MOVEIN}: {format(parseISO(pricingMoveInDate || new Date()), 'MMMM dd, yyyy')}
              </p>
            </div>
            <div style={{paddingTop: loading ? '400px' : ''}}>
              { !loading
                ? <CalendarGrid
                  calendarDays={calendarDays}
                  closeModal={closeModal}
                  saveModal={saveModal}
                  selectCalendarDate={selectCalendarDate}
                  selectedDate={pricingMoveInDate}
                  selectedTerm={pricingTerm}
                >
                </CalendarGrid>
                : <Spinner /> }
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

PricingCalendarModal.propTypes = {
  options: PropTypes.array.isRequired,
  selectedMoveInDate: PropTypes.string.isRequired,
  selectedTerm: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]).isRequired,
  closeModal: PropTypes.func.isRequired,
  saveModal: PropTypes.func.isRequired,
  bestValueTerm: PropTypes.object.isRequired,
  minDate: PropTypes.string.isRequired,
};

export default PricingCalendarModal;
