import coreApi from '../../config/api';
import { store } from '../../redux/store';

import { apiData } from '../../constants/Api/path';

const getConfiguration = () => {
  return store.getState().configuration;
};

export const submitRegistration = async (body) => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    const { data } = await coreApi.post(`${configuration.apiUrl}/${apiData.urlLeaseApp}`, body);
    return data;
  } catch (error) {
    throw error;
  }
};

export const getPmcInformation = async () => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    const { data } = await coreApi.get(`${configuration.apiUrl}/${apiData.urlPmcInfo}`);
    return data;
  } catch (error) {
    throw error;
  }
};
